.invoice-docket-container {
  .invoice-docket-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    .invoice-docket-form {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      background: white;
      padding: 40px 40px;
      box-shadow: 0px 0px 5px #efefef;
      font-size: 14px;
      .input-field {
        width: 300px;
        height: 200px;
        padding: 15px;
      }
      .button {
        margin-top: 20px;
        padding: 10px 0px;
        width: 180px;
      }
    }
  }
}
